import * as React from "react";
import {BrowserRouter, Routes, Route, Outlet, Link, HashRouter} from "react-router-dom";
import {TOC} from "../components/TOC";
import Book from "../components/Book";
import Stats from "../components/Stats";
import {Layout} from "./Layout";

export default function BookRouter() {

    return <HashRouter history={history}>
        <Routes>
            <Route path="/" element={<Layout/>}>
                <Route index element={<TOC/>}/>
                <Route path="/stats" element={<Stats/>}/>
                {
                    Array(18).fill(1)
                        .map((dummy, i) => i + 1)
                        .map(chapNum =>
                            <Route key={chapNum} path={`/${chapNum}`} element={<Book chapterNum={chapNum}/>}/>
                        )
                }
            </Route>
            <Route path="*" element={<Layout/>}/>
        </Routes>
    </HashRouter>
}