import React, {useState, useEffect} from 'react';

import {Chapter} from './Chapter';
import * as util from "../utils/Util";

function Book(props) {
    let {chapterNum} = props;
    chapterNum = chapterNum || 1;

    const [book, setBook] = useState(null);
    const [meaning, setMeaning] = useState(null);
    useEffect(() => {
        (async function f1() {
            let [slokasAndTitles, meaning] = await Promise.all([util.fetcher.slokas, util.fetcher.meaning]);
            setBook(slokasAndTitles);
            setMeaning(meaning)
        })();
    }, []);

    return book &&
        <Chapter name={book && book.title[chapterNum]}
                 number={chapterNum}
                 slokas={book && book.slokas[chapterNum]}
                 meaning={meaning && meaning.slokasMeaning[chapterNum]}
        />
}

export default Book;
