import audio1to5 from "../content/audio/Chapter-01-05.mp3";
import audio6to12 from "../content/audio/Chapter-06-12.mp3";
import audio13to18 from "../content/audio/Chapter-13-18.mp3";

let audioUrl = chapter => {
    let audioFile;
    switch (chapter) {
        case 1:
        case 2:
        case 3:
        case 4:
        case 5:
            audioFile = audio1to5;
            break;
        case 6:
        case 7:
        case 8:
        case 9:
        case 10:
        case 11:
        case 12:
            audioFile = audio6to12;
            break;
        case 13:
        case 14:
        case 15:
        case 16:
        case 17:
        case 18:
            audioFile = audio13to18;
            break;
    }
    return audioFile;
}

export function Audio(props) {
    return <audio id="song" preload="auto">
        <source id="songSource" src={audioUrl(props.chapNum) + "#t=466"} type="audio/mp3"/>
        Your browser does not support the audio element.
    </audio>
}