import React, {useState, useEffect} from 'react';

import {Sloka} from "./Sloka";
import {Audio} from "./Audio";
import {Col, Row} from "reactstrap";

export function Chapter(props) {
    if (!props.slokas) {
        return <></>
    }

    let {
        name,
        number,
        slokas,
        meaning
    } = props
    let slokaArr = Object.values(slokas)
    let meaningArr = meaning && Object.values(meaning)

    const [activeSloka, setActiveSloka] = useState()

    function updateActiveSloka(activeSlokaNum) {
        setActiveSloka(activeSlokaNum)
    }

    return <>
        <Audio chapNum={number}/>
        <Row className={"bgHeading"}>
            <Col>                {/*Chapter Heading*/}
                <span style={{fontSize: "1.5rem"}}>{name}</span>
            </Col>
        </Row>
        <Row style={{textAlign: "left", fontSize: 20, lineHeight: 1.8}}>
            <Col style={{padding: 0}}>                {/*Slokam Cards*/}
                {slokaArr.map((slokam, i) =>
                    <Sloka
                        chapNum={number}
                        slokaNum={i + 1}
                        slokam={slokam}
                        meaning={meaningArr && meaningArr[i]}
                        key={i + 1}
                        active={activeSloka}
                        updateActiveSlokaFn={updateActiveSloka}
                    />
                )}
            </Col>
        </Row>
        <Row>
            <Col>
                <div className="slokaMeaning">
                    ఓం తత్స ఇతి శ్రీమద్భగవద్గీతాసు ఉపనిషత్సు బ్రహ్మవిద్యాయాం యోగశాస్త్రే <br/>
                    శ్రీకృష్ణార్జున సంవాదే <Footer name={name} />
                </div>
            </Col>
        </Row>
    </>
}

function Footer(props) {
    let temp = props.name.split("–")
    return <>
        {temp[1].replace("గః", "గోనామ")}
        <p>{temp[0]}</p>
    </>
}