import React, {useState, useEffect} from 'react';
import {Button, Card, CardBody, CardHeader, Collapse} from "reactstrap";
import * as util from "../utils/Util";

let currentSloka;           // should be outside of this component
let isPlaying = false;
let nextSloka;
let listenOnce;

export function Sloka(props) {
    const [collapse, setCollapse] = useState(null);
    let {
        chapNum, slokaNum,
        slokam, meaning,
        updateActiveSlokaFn, active
    } = props

    let timeData;
    util.fetcher.audioTime.then(data => {
        timeData = data;
    });

    function nextSlokaTime() {
        let time = timeData.audio[chapNum][currentSloka + 1];
        if (!time) {
            return null;
        }

        let next = {
            num: currentSloka + 1,
            time: util.toSeconds(time)
        }

        console.log('current, next: ', currentSloka, next.num)
        return next;
    }

    function watchForNext() {
        nextSloka = nextSlokaTime();

        let showNextSlokaNum = function () {
            let currTime = Math.floor(song.currentTime);

            if(!nextSloka) {                        // means Chapter's Last sloka
                let chapEndTime = util.toSeconds(timeData.audio[chapNum][currentSloka]) + 15 + 20; // ex: calculating EOC (end of chap)
                if (currTime > chapEndTime) {       // stop playing @ end of chapter
                    song.pause();
                    console.log('--( Chapter end )--')
                    currentSloka = null;
                }

                return;
            }

            if (currTime < nextSloka.time) {    // playing current sloka
                return;
            }

            // show current sloka
            currentSloka = nextSloka.num;
            document.querySelector("#slokam" + currentSloka).scrollIntoView({behavior: "smooth"})
            updateActiveSlokaFn(currentSloka);
            nextSloka = nextSlokaTime();
        }

        if (listenOnce == null) {
            listenOnce = true;
            song.addEventListener('timeupdate', showNextSlokaNum);
        }
    }

    function playPause(clickedSlokaNum) {
        updateActiveSlokaFn(clickedSlokaNum);
        let startTime = timeData.audio[chapNum][clickedSlokaNum];

        if (!currentSloka || currentSloka !== clickedSlokaNum) {
            currentSloka = clickedSlokaNum;

            let url = document.querySelector("#songSource").src;
            let songWindow = "#t=466,486";

            url = url.split("#")[0] + songWindow;
            document.querySelector("#songSource").src = url;
            song.pause();
            song.currentTime = util.toSeconds(startTime)
            song.play();
            isPlaying = true;
            watchForNext();
        } else {
            if (isPlaying) {
                song.pause();
                isPlaying = false;
            } else {
                song.play();
                isPlaying = true;
            }
        }
    }

    function toggleOpen(e) {
        e.stopPropagation()
        let event = e.target.dataset.event;
        setCollapse(Number(event) === collapse ? 0 : Number(event))
    }

    return <Card key={slokaNum} id={"slokam" + slokaNum}
                 className="bgEven" style={{border: 0, backgroundColor: "unset"}}>
        <CardHeader onClick={() => playPause(slokaNum)} className="talapatra cardcss">
            {
                meaning &&
                <Button className="meaningButton rounded-circle"
                        style={{border: active === slokaNum ? "2px solid #906720" : 0, backgroundColor: "unset"}}
                        onClick={toggleOpen} data-event={slokaNum}>
                    {chapNum + "." + slokaNum}
                </Button>
            }
            {slokam.split("\\n").map((line, key) =>
                <span key={key}>
                    {line} <br/>
                </span>
            )}
        </CardHeader>
        <Collapse isOpen={slokaNum === collapse}>
            <CardBody style={{padding: 10}}>
                <div className="slokaMeaning">
                    {meaning && meaning.meaning}
                </div>
                <div className="slokaTranslation">
                    {meaning && meaning.translation}
                </div>
            </CardBody>
        </Collapse>
    </Card>;
}
